import React, { FC } from 'react';
import { Link } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';

import * as style from './index.module.scss';


const NotFoundPage: FC = () => {
    return (
        <div
            className={style.div}
        >
            <aside>
                <Link
                    to='#link'
                    className={style.link}
                >
                    常用連結
                </Link>
                <Link
                    to='#github'
                    className={style.link}
                >
                    Github 教學
                </Link>
            </aside>


            <h1>Chi ヽ(・∀・)ノ</h1>

            <section>
                <h2 id='link'>常用連結</h2>
                <a href='http://blog.SNinjo.com/chi/' target='_blank'>Chi Tutorial</a>
                <a href='https://github.com/SNinjo/Blog/tree/chi' target='_blank'>Github</a>
                <a href='https://github.com/SNinjo/Blog/tree/chi/content' target='_blank'>Articles in Github</a>
            </section>

            <section>
                <h2 id='github'>Github 教學</h2>
                <p>1. 先將 branch 切到專屬你編輯的 chi</p>
                <StaticImage
                    className={style.img}
                    src='./images/github1.png'
                    alt='tutorial'
                />
                <p>2. 點選 Content 資料夾 (文章都放在這)</p>
                <StaticImage
                    className={style.img}
                    src='./images/github2.png'
                    alt='tutorial'
                />
                <p>3. 點選要編輯的文章</p>
                <StaticImage
                    className={style.img}
                    src='./images/github3.png'
                    alt='tutorial'
                />
                <p>4. 點選編輯按鈕</p>
                <StaticImage
                    className={style.img}
                    src='./images/github4.png'
                    alt='tutorial'
                />
                <p>5. 改好文章後捲至下方, 填寫 changes 並按下 commit, 口以寫個 feedback 給我~</p>
                <StaticImage
                    className={style.img}
                    src='./images/github5.png'
                    alt='tutorial'
                />
            </section>

            <section>
                <p>灰常感謝你~ o(≧ω≦)o</p>
            </section>
        </div>
    )
}
export default NotFoundPage;


export const Head = () => (
    <title>Chi ヽ(・∀・)ノ</title>
)